<template>
  <div class="tick">
    <mt-header title="">
      <mt-button icon="back" @click="goback" slot="left"></mt-button>
    </mt-header>
    <div class="tick_content">
      <!--  -->
      <div class="tick_cinema_info">
        <div class="tick_cinema_infocontent">
          <div class="tick_cinema_infoname">
            {{ this.tmp_cinema.cinemaName }}
          </div>
          <div class="tick_cinema_infoaddress">
            {{ this.tmp_cinema.address }}
          </div>
        </div>

        <div class="tick_cinema_infomap" @click="gotoMap">
          <div class="mapicon fa fa-map-marker"></div>
          <div class="maptip"></div>
        </div>
      </div>
      <div class="tip">
        1.33D
      </div>
      <!--  -->
      <div class="tick_swiper">
        <!--  -->
        <div class="swiper_bg" v-if="movieList.length > 0">
          <img
            :src="movieList[currentIndex].pic_url"
            alt=""
            width="100%"
            height="100%"
          />
        </div>
        <!--  -->
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide"
              v-for="(item, index) in movieList"
              :key="index"
            >
              <img :src="item.pic_url" alt="" width="100%" height="100%" />
            </div>
          </div>
        </div>
        <div class="currentMovieTitle">{{ currentMovieTitle }}</div>
      </div>
      <!--  -->
      <div class="hall">
        <div class="hall-title">{{ currentTime }}</div>
        <div class="hall-list" v-if="movieList.length > 0">
          <template v-for="(item, index) in movieList[currentIndex].broadcast">
            <div class="hall-item" :key="index">
              <div class="hall-time">{{ item.time }}</div>
              <div class="hall-num">{{ item.hall }}</div>
              <div class="hall-price">{{ item.price }}</div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Swiper from "swiper";
import "swiper/dist/css/swiper.css";
import { mapGetters } from "vuex";
import Http from "../http";
import moment from "moment";
import store from "store";

export default {
  data: function() {
    return {
      imglist: [],
      movieList: [],
      currentIndex: 0,
      currentMovieTitle: "",
      currentTime: "",
      tmp_cinema: {}
    };
  },
  computed: {
    ...mapGetters("cinema", ["Cinema"])
    // swiperStyle: function() {
    //   if (this.movieList.length == 0) return;
    //   let currUrl = this.movieList[this.currentIndex].pic_url;
    //   return {
    //     // filter: "blur(5px)",
    //     backgroundImage: `url(${currUrl})`
    //   };
    // }
  },
  methods: {
    //   
    gotoMap() {
      console.log(this.Cinema);
      this.$router.push({ name: "map" });
    },
    //   
    goback() {
      this.$router.go(-1);
    },
    // 
    cinemasmovie: function() {
      Http.cinemasmovie(result => {
        this.movieList = result.data.result.lists;
        this.currentMovieTitle = this.movieList[this.currentIndex].movieName;
      });
    },
    calcDate() {
      let week = moment().format("e");
      let week_zh = "";

      switch (week) {
        case "0":
          week_zh = "";
          break;
        case "1":
          week_zh = "";
          break;
        case "2":
          week_zh = "";
          break;
        case "3":
          week_zh = "";
          break;
        case "4":
          week_zh = "";
          break;
        case "5":
          week_zh = "";
          break;
        case "6":
          week_zh = "";
          break;
        default:
          break;
      }
      this.currentTime = week_zh + " " + moment().format("MM-DD");
    },
    swiper() {
      var that = this;
      new Swiper(".swiper-container", {
        observer: true, //swiperswiper
        observeParents: true, //swiperswiper
        effect: "coverflow",
        centeredSlides: true,
        slidesPerView: 4,
        initialSlide: 0,
        coverflowEffect: {
          rotate: 0,
          stretch: -20,
          depth: 120,
          slideShadows: true
        },
        on: {
          slideChange: function() {
            that.currentIndex = this.activeIndex;
            //   console.log(that.currentIndex);
            that.currentMovieTitle = that.movieList[this.activeIndex].movieName;
          }
        }
      });
    }
  },
  mounted: function() {
    this.tmp_cinema = store.get("wanda_cinema");
    //   
    this.calcDate();
    // 
    this.cinemasmovie();
    // swiper
    this.swiper();
  }
};
</script>
<style lang="less">
.tick {
  //   height: 120px;
  .tick_content {
    position: absolute;
    top: 40px;
    width: 100%;
    height: calc(100% - 40px);
    overflow-y: scroll;
    .tick_cinema_info {
      height: 80px;
      display: flex;
      .tick_cinema_infocontent {
        flex: 1 1 auto;
        padding: 15px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .tick_cinema_infoname {
          font-size: 1.1rem;
        }
        .tick_cinema_infoaddress {
          font-size: 0.8rem;
          color: lightgray;
        }
      }
      .tick_cinema_infomap {
        flex: 0 0 50px;
        display: flex !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: grey;
        .mapicon {
          font-size: 1.5rem;
        }
        .maptip {
          font-size: 0.8rem;
        }
      }
    }
    .tip {
      height: 40px;
      font-size: 0.8rem;
      background-color: lightgoldenrodyellow;
      padding: 5px 10px;
      color: gray;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .tick_swiper {
      height: 160px;
      position: relative;
      .currentMovieTitle {
        position: absolute;
        bottom: 0px;
        text-align: center;
        width: 100%;
      }
      .swiper_bg {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 130px;
        img {
          filter: blur(5px);
        }
      }
      .swiper-container {
        position: absolute;
        top: 0px;
        left: 0px;
        padding-top: 10px;
        width: 100%;
        height: 120px;
      }
      .swiper-slide {
        height: 100%;
      }
    }
    .hall {
      .hall-title {
        height: 30px;
        line-height: 30px;
        font-size: 0.8rem;
        color: grey;
        margin-top: 15px;
        padding-left: 20px;
        background-color: lightgrey;
      }
      .hall-item {
        padding-left: 15px;
        padding-right: 15px;
        height: 60px;
        line-height: 60px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #ddd;
      }
    }
  }
}
</style>
